import {
    GET_PRODUCT,

} from './constants.js';

import axios from 'axios';

const { URL_BACK } = process.env;

export function getProducts(){
    return dispatch => {
        axios.get(`${URL_BACK}/product`)
        .then((result) => {
            return dispatch({
                type:GET_PRODUCT,
                payload:result.data
            })
        }).catch((err) => {
            console.log(`err`, err)
        });
    }
}