import {
    GET_PRODUCT,

} from './constants.js';


export const initialState = {

    productsOrigin: [],
    products:[]
}

export default function reducer(state= initialState, action){    
    if (action.type === GET_PRODUCT) {
        return{
            ...state,
            productsOrigin:action.payload,
            products:action.payload
        }
    }
    return state
}

