import { Typography } from "@mui/material";
import React from "react";
import { Button, Card, Input, Select } from "react-rainbow-components";

const inputStyles = {
    width: 210,
    
}

const cardStyle = {
    padding:'25px 5px 25px 5px',
    margin:'5px 0px 5px 0px',
    backgroundColor:'#f5f7f9'
}

const options = [
    { value: '0', label: 'Seleccionar' },
    { value: '1', label: 'Surco' },
    { value: '2', label: 'Surquillo' },
    { value: '3', label: 'san Martin' },
];

const Product = () => {
return (
    <div className="Product_principal">
        
        <Card style={cardStyle}>
            <Typography variant="h5" style={{color:'black'}}>
            
                Ingresa Datos
            
            </Typography>
            <br/>
            <Input
                label="Cargo"
                placeholder="Ingresar Cargo"
                type="text"
                className="rainbow-p-around_medium"
                style={inputStyles}
            />
             <Input
                label="Administrador"
                placeholder="Ingresar Administrador"
                type="text"
                className="rainbow-p-around_medium"
                style={inputStyles}
            />
             {/* <Input
                label="Empleado"
                placeholder="Ingresar Empleado "
                type="text"
                className="rainbow-p-around_medium"
                style={inputStyles}
            /> */}
             {/* <Input
                label="Direccion"
                placeholder="Ingresar Direccion "
                type="text"
                className="rainbow-p-around_medium"
                style={inputStyles}
            /> */}
             <Input
                label="Fecha Llegada"
                type="date"
                className="rainbow-p-around_medium"
                style={inputStyles}
            />
             {/* <Input
                label="Fecha Salida"
                type="date"
                className="rainbow-p-around_medium"
                style={inputStyles}
            /> */}
            {/* <Select
                    label="Distrito"
                    options={options}
                    id="example-select-1"
                    style={inputStyles}
                    className="rainbow-m-vertical_x-large rainbow-p-horizontal_medium rainbow-m_auto"
            /> */}


            <br/>
            <br/>
            <Button
            label="Registar"
            onClick={() => alert('clicked!')}
            variant="brand"
            className="rainbow-m-around_medium"
        />
        </Card>
    </div>
           
)
}

export default Product
